const EventModal = ({ visible, addEvent, cancel }) => {
	const [form] = Form.useForm();
	const onSubmit = values => {
		addEvent(values)
	}

	useEffect(() => {
    form.setFieldsValue(initialFormValues);
	});

	return (
		<Modal
      title="New Event"
			visible={visible}
			footer={null}
			destroyOnClose={true}
      onCancel={cancel}
    >
			<Form
        form={form}
				
        layout="vertical" 
				name="new-event"
				preserve={false}
				onFinish={onSubmit}
      >
				<Form.Item name="title" label="Title">
					<Input autoComplete="off" />
				</Form.Item>
				<Row gutter="16">
					<Col span={12} >
						<Form.Item name="start" label="Start">
							<TimePicker className="w-100" />
						</Form.Item>
					</Col>
					<Col span={12} >
						<Form.Item name="end" label="End">
							<TimePicker className="w-100" />
						</Form.Item>
					</Col>
				</Row>
				<Form.Item name="bullet" label="Label">
					<Select>
						{
							badgeColors.map(elm => (
								<Option value={elm} key={elm}>
									<Badge color={elm} />
									<span className="text-capitalize font-weight-semibold">{elm}</span>
								</Option>
							))
						}
					</Select>
				</Form.Item>
				<Form.Item className="text-right mb-0">
					<Button type="primary" htmlType="submit">
						Add Event
					</Button>
				</Form.Item>
			</Form>
		</Modal>
	)
}

const getFilteredDetails = async (config, type, school, branch) => {
	let body
	// message.loading({ content: 'Loading...', key })
	if (type === BY_TEACHER) {
		body = {
			"projection": [ "user" ],
			"groups": TEACHER_GROUP
		}

	} else {
		body = {
			"projection": [ "room" ]
		}

	}

	if (school)
		body.school = school

	if (branch)
		body.branch = branch

	try {
		const { data } = await axios.post(
			`${url}/api/utility/detail/`,
			body,
			config
		)
		// message.success({ content: 'Loaded!', key })
		return type === BY_TEACHER ? data.data.user : data.data.room
	} catch (err) {
		message.error({ content: Object.values(err.response.data), duration: 2 })
	}

}

const getCalendarData = async (config, month, year, school, branch, room, teacher, role) => {
	let api = `${url}/api/class/calendar/?filter=month&month=${month+1}&year=${year}`
	message.loading({ content: 'Loading...', key })
	
	if (school && role === SUPERADMIN)
		api += `&school=${school}`
	
	if (branch && role === SUPERADMIN || role === ADMIN)
		api += `&branch=${branch}`
	console.log('api', api)
	if (room && role !== TEACHER)
		api += `&room=${room}`
		
	if (teacher && role !== TEACHER)
		api += `&teacher=${teacher}`
	console.log('api', api)
	try {
		const { data } = await axios.get(
			api,
			config
		)
		message.success({ content: 'Success', key, duration: 2 })
		return data
	} catch (err) {
		message.error({ content: Object.values(err.response.data), key, duration: 2 })
		console.log(err.response.status)
		return {results: []}
	}
}

const pushData = (list, values, dateToEnter) => {
	const data = [{
		title: values.class_name ? values.class_name : 'Untitled Class',
		room: values.room_name ? values.room_name : 'Unknown Room',
		bullet: values.lesson_color,
		start: moment(values.start_time, ["HH:mm:ss"]).format('hh.mma'),
		end: moment(values.end_time, ["HH:mm:ss"]).format('hh.mma'),
	}]
	const newCalendarArr = list
	const isExistingDate = newCalendarArr.find(x => x.date === dateToEnter)
	if (isExistingDate) {
		for (let elm of newCalendarArr) { 
			if (elm.date === dateToEnter) {
				elm.lesson = [...elm.lesson, ...data]
			}
		}
	} else {
		newCalendarArr.push({date: dateToEnter, lesson: data})
	}
	const sortedNewCalendarArr  = newCalendarArr.sort((a,b) => moment(a.date) - moment(b.date))
	// setCalendarList(sortedNewCalendarArr)
	return sortedNewCalendarArr
}

const convertCalendarData = (data) => {
	let list = []
	if (data) {
		data.forEach(item => {
			const start_date = new Date(item.class_start_date)
			const end_date = new Date(item.class_end_date)
			const start_day = start_date.getDay()
			const end_day = end_date.getDay()
			const choosen_day = item.day === 6 ? 0 : item.day + 1

			for (let d = start_date; d <= end_date; d.setDate(d.getDate() + 1)) {
				if (d.getDay() === choosen_day) {
					// console.log(moment(new Date(d)).format('DD MMMM YYYY'))
					list = pushData(list, item, moment(new Date(d)).format('DD MMMM YYYY'))
				}
			}
		})
	}
	return list
}

const Schedule = () => {
	const today = new Date()
	const [calendarList, setCalendarList] = useState(CalendarData);
	const [modalVisible, setModalVisible] = useState(false);
	// const [selectedDate, setSelectedDate] = useState(null);
	const [selectedType, setSelectedType] = useState(BY_TEACHER)
	const [teacherOrRoomOptions, setTeacherOrRoomOptions] = useState([])
	const [selectedTeacher, setSelectedTeacher] = useState(null)
	const [selectedRoom, setSelectedRoom] = useState(null)
	const [selectedDate, setSelectedDate] = useState(today.getDate())      // is the DD of the month (not Mo, Tu, We, Th, Fr, Sa, Su)  
	const [selectedMonth, setSelectedMonth] = useState(today.getMonth())
	const [selectedYear, setSelectedYear] = useState(today.getFullYear())
	const [calendarData, setCalendarData] = useState([])
	const [loadingDetails, setLoadingDetails] = useState(false)
	const [detailsData, setDetailsData] = useState([])

	const token = useSelector(state => state.userLogin.token)
	const role = useSelector(state => state.userLogin.user.role_id)
	const schoolId = useSelector(state => state.userLogin.user.school)
	const branchId = useSelector(state => state.userLogin.user.branch)
	const schoolFilter = useSelector(state => state.filters.school)
	const branchFilter = useSelector(state => state.filters.branch)
	const config = {
		headers: {
			'Content-type': 'Application/json',
			'Authorization': `Bearer ${token}`
		}
	}

	useEffect(async () => {
		let res
		setLoadingDetails(true)
		if (role === SUPERADMIN) {
			setTeacherOrRoomOptions(await getFilteredDetails(config, selectedType, schoolFilter, branchFilter))
			res = await getCalendarData(config, selectedMonth, selectedYear, schoolFilter, branchFilter, selectedRoom, selectedTeacher, role)
			setCalendarData(await convertCalendarData(res.results))
		}
		if (role === ADMIN) {
			setTeacherOrRoomOptions(await getFilteredDetails(config, selectedType, schoolId, branchFilter, role))
			res = await getCalendarData(config, selectedMonth, selectedYear, schoolId, branchFilter, selectedRoom, selectedTeacher, role)
			console.log(res)
			setCalendarData(await convertCalendarData(res.results))
		}
		if (role === OPERATOR || role === TEACHER) {
			setTeacherOrRoomOptions(await getFilteredDetails(config, selectedType, schoolId, branchId))
			res = await getCalendarData(config, selectedMonth, selectedYear, schoolId, branchId, selectedRoom, selectedTeacher, role)
			setCalendarData(await convertCalendarData(res.results))
		}
		setLoadingDetails(false)
	}, [ selectedType, schoolFilter, branchFilter, selectedYear, selectedMonth, selectedRoom, selectedTeacher ])

	useEffect(async () => {
		setDetailsData(await takeSelectedCellData(calendarData, selectedDate))
	}, [selectedDate, calendarData])

	const takeSelectedCellData = (calendarData, selectedData) => {
		const selectedCellData = calendarData.find(elm => elm.date === selectedDate)
		return selectedCellData ? [selectedCellData] : []
	}

	const cellRender = value => {
		const listData = getListData(value.format((dateFormat)));
		return (
			<ul className="calendar-event">
				{listData.map((item, i) => (
					<li key={`${item.title}-${i}`}>
						<Badge color={item.bullet} text={item.title}/>
					</li>
				))}
			</ul>
		);
	}
	// let cal_data = convertCalendarData(calendarData)
	const getListData = (value) => {
		let listData = [];
		calendarData.forEach(elm => {
			if(elm.date === value) {
				listData = elm.lesson
			}
		})
		return listData;
	}

	const onSelect = value => {
		const selectedDate = value.format((dateFormat))
		setModalVisible(true);
		setSelectedDate(selectedDate)
	}

	const onDeleteEvent = (date, index) => {
		const data = calendarList.map(calendarList => {
			if(calendarList.date === date) {
				calendarList.lesson = calendarList.lesson.filter( (_, i) => i !== index)
			}
			return calendarList
		}).filter(elm => elm.lesson.length !== 0)
		setCalendarList(data)
	}

    const handleSelectChange = (e) => {
		setSelectedType(e)
		setSelectedTeacher(null)
		setSelectedRoom(null)
    }

	const handleTeacherChange = e => {
		if (e)
			setSelectedTeacher(e)
		else
			setSelectedTeacher(null)
	}

	const handleRoomChange = e => {
		if (e)
			setSelectedRoom(e)
		else
			setSelectedRoom(null)
	}

	const onAddEvent = values => {
		const data = [{
			title: values.title? values.title : 'Untitled Event',
			bullet: values.bullet,
			start: values.start.format(('HH:mm A')),
			end: values.end.format(('HH:mm A')),
		}]
		const newCalendarArr = calendarList
		const isExistingDate = newCalendarArr.find(x => x.date === selectedDate)
		if (isExistingDate) {
			for (let elm of newCalendarArr) { 
				if (elm.date === selectedDate) {
					elm.lesson = [...elm.lesson, ...data]
				}
			}
		} else {
			newCalendarArr.push({date: selectedDate, lesson: data})
		}
		const sortedNewCalendarArr  = newCalendarArr.sort((a,b) => moment(a.date) - moment(b.date))
		setModalVisible(false)
		setCalendarList(sortedNewCalendarArr)
	}

	const onAddEventCancel = () => {
		setModalVisible(false)
	}

	return (
		<Card className="calendar mb-0">
			<Row>
				<Col xs={24} sm={24} md={9} lg={6}>
					<h2 className="mb-4">Details</h2>
						<Skeleton active={true} loading={loadingDetails}>
						{
							calendarData
							? <DetailList 
								list={detailsData} 
								cur_year={today.getFullYear()}
								onDelete={onDeleteEvent}
							/>
							: <h4>You do not have task today</h4>
						}
						</Skeleton>
				</Col>
				<Col xs={24} sm={24} md={15} lg={18}>
					<Calendar
                        fullscreen={true}
                        onSelect={val => onSelect(val)} 
                        dateCellRender={cellRender}
                        headerRender={({ value, type, onChange, onTypeChange }) => {
                            const start = 0;
                            const end = 12;
                            const monthOptions = [];

                            const current = value.clone();
                            const localeData = value.localeData();
                            const months = [];
                            for (let i = 0; i < 12; i++) {
                            current.month(i);
                            months.push(localeData.monthsShort(current));
                        }

                        for (let index = start; index < end; index++) {
                        monthOptions.push(
                            <Select.Option className="month-item" key={`${index}`}>
                            {months[index]}
                            </Select.Option>,
                        );
                        }
                        setSelectedMonth(value.month());

                        setSelectedYear(value.year());
                        const options = [];
                        for (let i = selectedYear - 10; i < selectedYear + 10; i += 1) {
                        options.push(
                            <Select.Option key={i} value={i} className="year-item">
                            {i}
                            </Select.Option>,
                        );
                        }
                        return (
                        <div style={{ padding: 10 }} className={`ant-picker-calendar-header _calendar-header ${role === TEACHER ? "_calendar-header-teacher" : ""}`}>
						{
						role !== TEACHER &&
                            <Row type="flex">
                            <Col>
                                <Select defaultValue={BY_TEACHER} className="ant-select-md _calendar-show-type-select" onChange={handleSelectChange}>
                                    <Option value={BY_TEACHER}>Teacher</Option>
                                    <Option value={BY_ROOM}>Room</Option>
                                </Select>
                            </Col>
							<Col>
								{
									selectedType === BY_TEACHER ?
									<Select
										allowClear
										placeholder="Select a teacher"
										onChange={handleTeacherChange}
										className="ant-select-md filter_by_teacher_room"
										showSearch
										value={selectedTeacher}
										filterOption={(input, option) =>
											option.props.children.toString().toLowerCase().indexOf(input.toLowerCase()) >= 0
										}
									>
										{
											teacherOrRoomOptions && teacherOrRoomOptions.map(teacher => {
												return <Option value={teacher.id} key={teacher.id}>
													<img style={{ position: 'relative', width: '20px', height: '20px', transform: 'translate(-4px, -2px)' }} src="https://img.icons8.com/windows/452/showing-small-size.png" />
													{teacher.lastname && teacher.lastname[0]}. {teacher.firstname}
													</Option>
											})
										}
									</Select>
									:
									<Select
										allowClear
										placeholder="Select a room"
										onChange={handleRoomChange}
										className="ant-select-md filter_by_teacher_room"
										showSearch
										value={selectedRoom}
										filterOption={(input, option) =>
											option.props.children.toString().toLowerCase().indexOf(input.toLowerCase()) >= 0
										}
									>
										{
											teacherOrRoomOptions && teacherOrRoomOptions.map(room => {
												return <Option value={room.id} key={room.id}>
													{room.name}
													</Option>
											})
										}
									</Select>
								}
							</Col>
                            </Row>
						}
                            <Row type="flex">
                            <Col>
                                <Select
                                size="medium"
                                dropdownMatchSelectWidth={false}
                                className="my-year-select _calendar-year-select"
                                onChange={newYear => {
									setSelectedYear(newYear)
                                    const now = value.clone().year(newYear);
                                    onChange(now);
                                }}
                                value={String(selectedYear)}
                                >
                                {options}
                                </Select>
                            </Col>
                            <Col>
                                <Select
                                size="medium"
                                dropdownMatchSelectWidth={false}
                                className="_calendar-month-select"
                                value={String(selectedMonth)}
                                onChange={selectedMonth => {
                                    const newValue = value.clone();
                                    newValue.month(parseInt(selectedMonth, 10));
                                    onChange(newValue);
                                }}
                                >
                                {monthOptions}
                                </Select>
                            </Col>
                            <Col>
                                <Group size="medium" className="_calendar-mode-switch" onChange={e => onTypeChange(e.target.value)} value={type}>
                                <Btn className="radio-btn" value="month">Month</Btn>
                                <Btn className="radio-btn" value="year">Year</Btn>
                                </Group>
                            </Col>
                            </Row>
                        </div>
                        );
                    }}
                    />
				</Col>
			</Row>
			{/* <EventModal 
				visible={modalVisible}
				addEvent={onAddEvent}
				cancel={onAddEventCancel}
			/> */}
			<hr className="d-print-none"/>
			<div className="text-right d-print-none">
				<Button type="primary" onClick={() => window.print()}>
					<PrinterOutlined  type="printer" />
					<span className="ml-1">Print</span>
				</Button>
			</div>
		</Card>
	)
}

export default Schedule

